.list-item {

    width: 225px;
    height: 120px;
    background-color: var(--main-color);
    margin-right: 5px;
    overflow: hidden;
    cursor: pointer;
    color: white;
    transition: 0.5s;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    video {
        width: 100%;
        height: 140px;
        object-fit: cover;
        // position: absolute;
        // top: 0;
        // left: 0;
    }

    &:hover {
        width: 325px;
        height: 320px;
        -webkit-box-shadow: 1px 3px 21px -1px rgba(245, 240, 245, 0.62);
        -moz-box-shadow: 1px 3px 21px -1px rgba(245, 240, 245, 0.62);
        box-shadow: 1px 3px 21px -1px rgba(245, 240, 245, 0.62);
        border-radius: 5px;

        img {
            display: none;
        }
    }

    .item-info {
        display: flex;
        flex-direction: column;
        align-items: start;
        gap: 5px;
        padding: 0 10px;
        background-color: var(--main-color);
        text-decoration: none;


        span {
            margin-left: 1rem;
            text-decoration: none;
        }

        .limit {
            border: solid 1px gray;
            text-decoration: none;
        }

        .desc {
            font-size: 13px;
            margin-bottom: 10px;
            text-decoration: none;
        }

        .genre {
            color: lightgray;
            text-decoration: none;
        }

        .icons {
            margin-left: 0.5rem;
            display: flex;
            justify-content: space-between;
            width: 40%;

            .ic {
                transition: 0.5s;
                font-size: 1.5rem;
                color: gray;

                &:hover {
                    color: red;
                    transform: scale(1.2);
                    border-color: red;
                }
            }
        }
    }
}