.navbar {
    color: White;
    font-size: 14px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 999;
    background: linear-gradient(to top, transparent 0%, rgb(0, 0, 0, 0.3) 50%);
    transition: 1s;

    &.scrolled {
        background-color: var(--main-color);
    }

    .container {
        padding: 0 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;



        .left {
            display: flex;
            align-items: center;

            span {
                margin-right: 20px;
                cursor: pointer;
                text-decoration: none;
            }

            img {
                height: 25px;
                margin-right: 40px;
            }

        }

        .right {
            display: flex;
            align-items: center;

            img {
                height: 30px;
                width: 30px;
                object-fit: cover;
                cursor: pointer;
                border-radius: 50%;
            }

            .profile {
                .options {
                    display: none;
                    background-color: var(--main-color);
                    border-radius: 5px;
                }

                span {
                    padding: 10px;
                    cursor: pointer;
                }

                &:hover {
                    .options {
                        display: flex;
                        flex-direction: column;
                        position: absolute;
                    }
                }
            }
        }

        .icon {
            margin: 0 15px;
            cursor: pointer;
        }
    }

}