.watch {
    width: 100vw;
    height: 100vh;
    background-color: beige;

    .back {
        display: flex;
        align-items: center;
        position: absolute;
        top: 10px;
        left: 10px;
        color: white;
        cursor: pointer;
        z-index: 2;
        justify-content: flex-start;
        gap: 10px;
        transition: 0.5s;

        &:hover {
            transform: scale(1.1);
        }
    }



    .video {
        width: 100vw;
        height: 100vh;
        object-fit: cover;
    }
}