.register {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
            rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%),
        url("https://springboard-cdn.appadvice.com/wp-content/appadvice-v2-media/2016/11/Netflix-background_860c8ece6b34fb4f43af02255ca8f225.jpg");

    background-size: cover;
    position: relative;

    .top {



        .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 20px 50px;


            .logo {
                height: 40px;
                cursor: pointer;

            }


            .login-btn {
                text-decoration: none;
                background-color: red;
                border-radius: 5px;
                border: none;
                padding: 5px 15px;
                color: white;
                cursor: pointer;
                font-size: 20px;
                transition: 0.5s;
                z-index: 2;

                &:hover {
                    transform: scale(1.1)
                }
            }
        }
    }

    .container {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: white;

        h1 {
            font-size: 50px;
        }

        h2 {
            margin: 20px;
        }

        p {
            font-size: 20px;
        }

        .input {
            width: 50%;
            background-color: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 20px;
            height: 50px;
            border-radius: 5px;
            border: none;

            input {
                flex: 9;
                border: none;
                height: 100%;
                padding: 0 10px;
                font-size: 15px;
            }

            .register-btn {
                flex: 3;
                height: 100%;
                font-size: 20px;
                background-color: red;
                color: white;
                border: none;
                cursor: pointer;
                transition: 0.5s;

                &:hover {
                    font-size: 22px;
                }
            }
        }
    }

}