.featured {
    height: 90vh;
    position: relative;
    display: flex;
    flex-direction: column;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }

    .info {
        width: 30%;
        position: absolute;
        left: 50px;
        bottom: 100px;
        color: white;
        display: flex;
        flex-direction: column;

        img {
            width: 400px;
        }

        .desc {
            margin: 20px 0;
        }

        .buttons {
            display: flex;
            background-color: transparent;

            button {

                padding: 10px 20px;
                border: none;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: bolder;
                margin-right: 10px;
                cursor: pointer;

                &.play {
                    background-color: white;
                    color: var(--main-color);
                }

                &.info-btn {
                    background-color: gray;
                    color: white;
                }

                span {
                    margin-left: 5px;
                }
            }

        }
    }

    .category {
        position: absolute;
        top: 80px;
        left: 50px;
        font-size: 30px;
        font-weight: 500;
        color: white;
        display: flex;
        align-items: center;


        select {
            cursor: pointer;
            background-color: var(--main-color);
            border: 1px solid white;
            color: white;
            margin-left: 20px;
            padding: 5px;
        }
    }



}