.list {
    width: 100%;
    margin-top: 10px;

    .list-title {
        font-size: 20px;
        color: white;
        font-weight: 500;
        margin-left: 150px;
    }

    .wrapper {
        position: relative;

        .container {
            margin-left: 50px;
            margin-top: 10px;
            display: flex;
            width: max-content;
            transition: all 1s ease;
        }

        .slider-arrow {
            position: absolute;
            width: 50px;
            height: 100%;
            background-color: rgb(22, 22, 22, 0.5);
            color: white;
            z-index: 999;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: pointer;

            &.left {
                left: 0;
            }

            &.right {
                right: 0;
            }
        }


    }
}